<template>
  <v-container class="py-5">
    <v-card>
      <v-card-title>
        Registrations For Year {{ academicYear }}
        <v-spacer></v-spacer>
      </v-card-title>
      <v-card-title>
        <v-select
          :items="status"
          v-model="search.status"
          label="Status"
          outlined
          dense
          class="ma-2"
          hide-details
          clearable
          @change="buildTerms()"
          @click:clear="clearSearch('status')"
        ></v-select>
        <v-text-field label="Name" class="ma-2" v-model="search.name" dense outlined hide-details clearable @change="buildTerms()" @click:clear="clearSearch('name')" />
        <v-text-field label="Registration Number" class="ma-2" v-model="search.regnum" dense outlined hide-details clearable @change="buildTerms()" @click:clear="clearSearch('regnum')" />
        <v-select
          :items="streams"
          v-model="search.stream"
          label="Stream"
          outlined
          dense
          class="ma-2"
          hide-details
          clearable
          @change="buildTerms()"
          @click:clear="clearSearch('stream')"
        ></v-select>
        <v-select
          :items="units"
          v-model="search.unit"
          label="Unit"
          outlined
          dense
          class="ma-2"
          hide-details
          clearable
          @change="buildTerms()"
          @click:clear="clearSearch('unit')"
        ></v-select>
      </v-card-title>
      <v-card-subtitle
        v-if="error_message"
        class="white--text error"
      >{{ error_message }}</v-card-subtitle>
      <v-card-text>
        <v-data-table
          style="cursor: pointer"
          :headers="headers"
          :items="results"
          item-key="id"
          :options.sync="options"
          :server-items-length="total"
          :footer-props="{ 'items-per-page-text':$t('App.hardcoded-texts.tableText'), 'items-per-page-options': [5,10,20,50] }"
          :loading="loading"
          class="elevation-1"
          @click:row="clickIt"
        >
          <template v-slot:item.status="{ item }">
            <v-chip
              :color="getColor(item.status)"
              dark
            >
              {{ item.status }}
            </v-chip>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
export default {
  data: function() {
    return {
      status: [{
        text: "Pending",
        value: "pending"
      }, {
        text: "Approved",
        value: "approved"
      }, {
        text: "Reject",
        value: "reject"
      }],
      streams: [{
        text: "Ummahaati",
        value: "ummahaati-U"
      }, {
        text: "Mutafarrighati",
        value: "mutafarrighati-T"
      }, {
        text: "Banaati",
        value: "banaati-N"
      }, {
        text: "Rijaal Boarding",
        value: "rijaal-B"
      }, {
        text: "Rijaal Day",
        value: "rijaal-D"
      }, {
        text: "Baniiyn Boarding",
        value: "baniiyn-BN"
      }, {
        text: "Baniiyn Day",
        value: "baniiyn-DN"
      }],
      units: [{
        text: "Wanawake Watu Wazima",
        value: "women"
      }, {
        text: "Wanaume Watu Wazima",
        value: "men"
      }, {
        text: "Watoto",
        value: "kids"
      }],
      search: {
        status: '',
        name: '',
        regnum: '',
        stream: '',
        unit: ''
      },
      debug: "",
      terms: {},
      headers: [{
        text: "Registration Number",
        value: "regnum"
      }, {
        text: "Surname",
        value: "surname"
      }, {
        text: "Given Name(s)",
        value: "given"
      }, {
        text: "Birth Date",
        value: "birthDate"
      }, {
        text: "Gender",
        value: "gender"
      }, {
        text: "Status",
        value: "status"
      }, {
        text: "Stream",
        value: "stream"
      }, {
        text: "Accomodation",
        value: "accomodation"
      }, {
        text: "Paid",
        value: "paid"
      }, {
        text: "Debt",
        value: "pending"
      }],
      results: [],
      options: { itemsPerPage: 10 },
      loading: false,
      total: 0,
      prevPage: -1,
      link: [],
      error_message: null,
      update_again: { rerun: false, restart: false },
      startDate: "",
      endDate: "",
      academicYear: ""
    };
  },
  watch: {
    options: {
      handler() {
        this.getData();
      },
      deep: true
    }
  },
  mounted: function() {
    this.getData(true);
  },
  methods: {
    getColor(status) {
      if(status === "Pending") {
        return "warning"
      } else if(status === "Rejected") {
        return "error"
      } else if(status === "Approved") {
        return "success"
      }
      return "grey"
    },
    clearSearch: function(field) {
      this.search[field] = ''
      this.buildTerms()
    },
    clickIt: function(record) {
      this.$router.push("/custom/registration_view?path=/itsf&role=" + record.id + '&practitioner=' + record.practitioner);
    },
    checkRerun() {
      if ( !this.loading && this.update_again.rerun ) {
        this.getData( this.update_again.restart )
        this.update_again = { rerun: false, restart: false }
      }
    },
    async buildTerms() {
      this.terms = {}
      if(this.search.name || this.search.regnum) {
        await this.searchPractitioner()
      }
      if(this.search.status) {
        this.terms["regstatus"] = this.search.status
      }
      if(this.search.stream) {
        this.terms["stream"] = this.search.stream
      }
      if(this.search.unit) {
        this.terms["unit"] = this.search.unit
      }
      this.getData(true);
    },
    searchPractitioner() {
      return new Promise((resolve, reject) => {
        let search = ""
        if(this.search.regnum) {
          search = "regnum=" + this.search.regnum
        }
        if(this.search.name) {
          if(search) {
            search += "&name:contains=" + this.search.name
          } else {
            search = "name:contains=" + this.search.name
          }
        }
        let url = "/fhir/Practitioner?" + search
        fetch(url).then(response => {
          response.json().then(async (data) => {
            if(data.entry) {
              for (let entry of data.entry) {
                this.terms["practitioner"] = "Practitioner/" + entry.resource.id
              }
            } else {
              this.terms["practitioner"] = "Practitioner"
            }
            resolve()
          }).catch(() => {
            reject()
          })
        }).catch(() => {
          reject()
        })
      })
    },
    getData(restart) {
      //console.log("getting data",restart)
      if ( this.loading ) {
        this.update_again.rerun = true
        this.update_again.restart = this.update_again.restart || restart
        return
      }
      this.loading = true;
      this.error_message = null;
      let url = "";
      if (restart) this.options.page = 1;
      if (this.options.page > 1) {
        if (this.options.page === this.prevPage - 1) {
          url = this.link.find(link => link.relation === "previous").url;
        } else if (this.options.page === this.prevPage + 1) {
          url = this.link.find(link => link.relation === "next").url;
        }

        url = url.replace("/fhir?","/fhir/PractitionerRole?")
        url = url.substring(url.indexOf("/fhir/"));
        //some of the hapi instances requires _total=accurate to always be available for them to return total resources
        if(url.indexOf('_sort=_lastUpdated') === -1) {
          url = url + '&_sort=_lastUpdated'
        }
        if(url.indexOf('_total=accurate') === -1) {
          url = url + '&_total=accurate'
        }
        // url += "&date=" + this.endDate
      }
      if (url === "") {
        let count = this.options.itemsPerPage || 10;
        let sort = "";
        for (let idx in this.options.sortBy) {
          if (sort) {
            sort += ",";
          }
          if (this.options.sortDesc[idx]) {
            sort += "-";
          }
          sort += this.options.sortBy[idx];
        }
        url =
          "/fhir/PractitionerRole?_count=" +
          count +
          "&_elements=practitioner,name,birthDate,gender,extension,identifier&date=ge" + this.endDate +
          "&_include=PractitionerRole:practitioner&_sort=_lastUpdated&_total=accurate"
        this.debug = url;
      }
      let sTerms = Object.keys(this.terms);
      for (let term of sTerms) {
        if ( Array.isArray( this.terms[term] ) ) {
          if ( this.terms[term].length > 0 ) {
            url += "&" + term + "=" + this.terms[term].join(',')
          }
        } else if ( this.terms[term] ) {
          url += "&" + term + "=" + this.terms[term];
        }
      }

      this.prevPage = this.options.page;
      //console.log("fetching",url)
      fetch(url).then(response => {
        response.json().then(async (data) => {
          this.results = [];
          if (data.total > 0) {
            this.link = data.link;
            for (let entry of data.entry) {
              if(entry.resource.resourceType !== 'PractitionerRole') {
                continue
              }
              let result = { id: entry.resource.id };
              result.status = entry.resource.extension.find((ext) => {
                return ext.url === "http://ihris.org/fhir/StructureDefinition/registration-status"
              })?.valueCoding?.display
              result.stream = entry.resource.extension.find((ext) => {
                return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-maahad-stream"
              })?.valueCoding?.display
              result.accomodation = entry.resource.extension.find((ext) => {
                return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-acommodation"
              })?.valueCoding?.display
              result.paid = entry.resource.extension.find((ext) => {
                return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-paid-fee"
              })?.valueInteger
              if(result.paid) {
                result.paid = result.paid.toLocaleString()
              }
              result.pending = entry.resource.extension.find((ext) => {
                return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-unpaid-fee"
              })?.valueInteger
              if(result.pending) {
                result.pending = result.pending.toLocaleString()
              }
              let practitioner = data.entry.find((other) => {
                return other.resource.resourceType === "Practitioner" && other.resource.id === entry.resource.practitioner.reference.split("/")[1]
              })
              if(practitioner) {
                result.practitioner = practitioner.resource.id
                result.regnum = practitioner.resource.extension.find((ext) => {
                  return ext.url === "http://ihris.org/fhir/StructureDefinition/registration-number"
                })?.valueString
                result.surname = practitioner.resource.name[0].family
                result.given = practitioner.resource.name[0].given.join(", ")
                result.birthDate = practitioner.resource.birthDate
                result.gender = await this.$fhirutils.lookup( practitioner.resource.gender, 'http://hl7.org/fhir/administrative-gender' )
              }
              this.results.push(result);
            }
          }
          this.total = data.total;
          this.loading = false;
          this.checkRerun()
        }).catch(err => {
          this.loading = false;
          this.error_message = "Unable to load results.";
          this.checkRerun()
          console.log(err);
        });
      }).catch(err => {
        this.loading = false;
        this.error_message = "Unable to load results.";
        this.checkRerun()
        console.log(err);
      });
    }
  },
  created() {
    fetch("/site/student/getAcademicYear").then((response) => {
      response.json().then((response) => {
        this.academicYear = response.academicYear
        let end = response.end.split("-")
        let year = parseInt(end[0]) - 1
        this.startDate = year + "-" + end[1] + "-" + end[2]
        this.endDate = response.end
      })
    })
  }
};
</script>
